
@font-face {
	font-family: "PT SANS";
	font-style: normal;
	font-display: swap;
	font-weight: 400;
	src: local("PT SANS"),
	  url("../fonts/ptsans.woff") format("woff"),
	  url("../fonts/ptsans.woff2") format("woff2");

}


@font-face {
	font-family: "PT SANS";
	font-style: normal;
	font-display: swap;
	font-weight: 700;
	src: local("PT SANS Bold"),
	  url("../fonts/ptsansbold.woff") format("woff"),
	  url("../fonts/ptsansbold.woff2") format("woff2");
}

.page__body {
    width: 320px;
	margin: 0;
	padding: 0;
	font-family: "PT SANS";
	font-size: 18px;
	font-weight: 700;
	text-transform: uppercase;
	margin-top: 223px;
}

body {
    // background-color: antiquewhite;
    background-color: @main-color;
}

.visually-hidden {
	position: absolute;
    clip: rect(0 0 0 0);
    width: 1px;
    height: 1px;
    margin: -1px;
    overflow: hidden;
}